
import Picker from 'vux/src/components/picker/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
Picker,
PopupPicker,
XButton
},
data(){
return {
account: '',
pass: '12345678',
pickerList:[{
name: '党支部书记',
value: "3"
}, {
name: '片区负责人',
value: "2"
}],
pickerValue: [""],
pickerName : "",
roleName : "请选择角色",
PickerVisible:false
}
},
methods:{
login() {
if (this.pickerValue[0] == "0")
return this.$vux.toast.show({
text: '请选择角色',
type: 'text',
position: 'top'
});
if (!this.account)
return this.$vux.toast.show({
text: '请输入帐号',
type: 'text',
position: 'top'
});
if (!this.pass)
return this.$vux.toast.show({
text: '请输入密码',
type: 'text',
position: 'top'
});
this.$store.dispatch('login', {
name: this.account,
password: this.pass,
openId: this.$route.query.openId || undefined,
userType : this.pickerValue[0]
}).then(result => {
if(result.status){
var roleId = result.entry.roleId;
sessionStorage.userRoleId = roleId;
if(roleId == 2)
{
this.$router.push({
path: 'points/review'
})
}
else if(roleId == 3)
{
this.$router.push({
path: 'points/evaluation'
})
}
else
{
this.$router.push({
path: '/main' + roleId
})
}

}else{
this.$vux.toast.show({
text: result.message,
width: '18em',
type: 'text',
position: 'top'
})
}
}
)
},
keyUpReturn()
{
this.login();
},
pickerChange(closeType){
if(closeType)
{
console.log("",closeType)
}
},
roleChange(value){
this.pickerValue = value;
if(value == "3")
{
this.roleName = this.pickerList[0].name;
}
else if(value == "2")
{
this.roleName = this.pickerList[1].name;
}
}
},
mounted(){
document.body.classList.add('login-manage');
},
beforeDestroy () {
document.body.classList.remove('login-manage');
}
}



